html {
  font-size: 10px;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  text-align: center;
  color: #333333;
  margin: 0;
}

h1 {
  font-size: 4rem;
  margin-bottom: 0;
}

h2 {
  font-size: 3.25rem;
}

a {
  color: #477ca7;
}
a:visited {
  color: #74638f;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
  margin: 0 auto;
  border-radius: 30px;
}

.main {
  margin: 30px;
  padding: 15px;
  border-radius: 30px;
  background: #f1f1f1;
}

.img-div {
  padding: 10px;
  margin: 0;
}

.img-caption {
  margin: 15px 0 5px 0;
}

.headline {
  margin: 50px 0;
  text-align: center;
  font-size: 2.3rem;
}

ul {
  max-width: 550px;
  margin: 0 auto 50px auto;
  text-align: left;
  line-height: 1.6;
}

li {
  margin: 16px 0;
}

blockquote {
  font-style: italic;
  max-width: 545px;
  margin: 0 auto 50px auto;
}

.footer p {
  font-weight: bold;
  font-size: 2rem;
  padding: 1rem;
}

@media only screen and (width <= 576px) {
  h1 {
    font-size: 3.5rem;
    line-height: 1.2;
  }
  .main {
    margin: 0;
  }
  .img-caption {
    font-size: 1.4rem;
  }
}